<template>
  <div class="report">

    <div class="head">
      举报<span>{{item.type | _type}}</span>的理由 : <span>{{item.report_type | report_type}}</span>
    </div>



  <div class="showComment" @click="toRead(item.report_article_id)">
    <!-- 头像-->
    <UserTag :msgg="item.user_info"></UserTag>
    <!-- <HeadPor class="headPor"
    :msgg="{ authen:item.comment.user_authen, img:item.comment.user_img,name:item.comment.user_name,user_lv:item.comment.user_lv,gold:item.comment.user_gold}"></HeadPor> -->
    <!-- 下部 -->
    <div class="bottom">
      <!-- 时间-->
      <div>
        <i class="iconfont time">&#xe607;</i>
        <span class="spanOne">{{item.report_date|fTime}}</span>
      </div>
      <!-- 几楼 -->
      <div>
        <span v-if='item.user_info.comment_index'>
            {{item.user_info.comment_index}}楼
        </span>
      </div>
    </div>
    <!-- 如果是举报回复评论的评论 @click="toRead(item.info_article_id)"......................................-->
    <div v-if="item.type == 'commentForComment'" >
      <!-- 原来的评论-->
      <div class="replyComment" >
        <div>回复评论</div>
        <!-- 被回复的评论内容 class="re_content "-->
        <div :class="['re_content',!item.user_info.replyTemp.comment_content?'del_content':'']"
        v-html="$fn_empty_c(item.user_info.replyTemp.comment_content)">
          <!-- {{item.user_info.replyTemp.comment_content| fNoComment}} -->
        </div>
      </div>
      <!-- 评论 class="commentContent"-->
      <div :class="['commentContent',!item.user_info.comment_content?'del_content':'']"
      v-html="$fn_empty_c(item.user_info.comment_content)">
        <!-- {{item.user_info.comment_content| fNoComment}} -->
      </div>
    </div>
    <!-- 如果举报回复文章的评论 .....@click="toRead(item.info_article_id)"..........................................-->
    <div v-if="item.type == 'commentForArticle'" >
      <!-- 原来的评论-->
      <div class="replyComment" >
        <div>回复文章</div>
        <!-- 被回复的评论内容 class="re_content"-->
        <div :class="['re_content',!item.article_title?'del_content':'']">
          {{item.article_title| fNoArticle}}
        </div>
      </div>
      <!-- 评论 class="commentContent"-->
      <div :class="['commentContent',!item.user_info.comment_content?'del_content':'']"
      v-html="$fn_empty_c(item.user_info.comment_content)">
        <!-- {{item.user_info.comment_content| fNoComment}} -->
      </div>
    </div>
    <!-- 如果是举报文章11111111111111111111111111111-->
    <div v-if="item.type == 'article'" class="reportArticle">
      {{item.user_info.article_title | fNoArticle }}
    </div>
    <!-- <SignUp></SignUp> -->
  </div>


<div class="nextReport" @click="nextReport">
        下一个
    </div>
  </div>
</template>

<script>
	/* 引入头像组件*/
	// import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"

  // import {createNamespacedHelpers} from 'vuex'
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    activated(){//firstEnter  fromPath
      if(this.firstEnter > 1 && (this.fromPath != '/read'
      // && this.fromPath != '/getArticleReward'
      && this.fromPath != '/search')){
        // alert(1)
        this.isBuffering = true
        /* 如果不是在第一次进入并且...*/
        this.mountedFn()
      }else{
        this.firstEnter ++
      }
    },
    /* 数据*/
    data(){
      return {
        item : {
          user_info : {}
        },
        ws_report : false,
        /* 第一次进入页面*/
        firstEnter : 0
      }
    },
    filters:{
      report_type:function(value){
        switch(value){
          case "abuse":
            return  '人身攻击'
          break;
          case "not18":
            return  '色情信息'
          break;
          case "ab":
            return  '垃圾广告'
          break;
          case "party":
            return  '政治敏感'
          break;
        }
      },
      _type : function(value){
        switch(value){
          case "commentForArticle":
            return  '评论'
          break;
          case "commentForComment":
            return  '评论'
          break;
          case "article":
            return  '文章'
          break;
        }
      }
    },
    /* 方法*/
    methods:{
      nextReport(){
        this.getReport()
        this.isBuffering = true
      },
      mountedFn(){
        this.firstEnter ++
        this.isBuffering = true
        this.getReport()
      },
      /* 获得举报*/
      getReport(){
        if(this.ws_report == true){
          this.$toast('正在获取中...')
          return;
        }
        this.ws_report = true

        this.$api.article.doReport({}).then(
            res =>{
              this.ws_report = false
              this.isBuffering = false
              if(res.data == 'no_data'){
                this.$toast('没有举报了')
              }else{
                console.log(res.data,'处理举报')
                res.data.report_date = this.$formatDate(new Date(res.data.report_date))
                if(res.data.user_info_deleted){
                  /* 如果评论已经被删除*/
                  res.data.user_info = {
                    replyTemp :{}
                  }
                }else{
                }
                this.item = res.data
                    // console.log(res.data,'处理举报')
                    // res.data[0].report_date = this.$formatDate(new Date(res.data[0].report_date))
                    // if(res.data[0].user_info_deleted){
                    //   /* 如果评论已经被删除*/
                    //   res.data[0].user_info = {
                    //     replyTemp :{}
                    //   }
                    // }else{
                    // }
                    // this.item = res.data[0]
              }
            }
        )
      },
      toRead(params){
        this.$router.push({
          path: '/read',
          query : {
            articleId : params
          }
        })
      },
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
    },
    /* 计算*/
    computed:{
      isBuffering:{
       get () {
             return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
       },
       set (value) {
         this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
       }
      },
      fromPath:{
        get () {
              return this.$store._modulesNamespaceMap['path/'].context.state.fromPath
        },
        set (value) {
          this.$store._modulesNamespaceMap['path/'].context.commit('fromPathHandler', value)
        }
      },
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // HeadPor
      UserTag
    },
    /* 挂载阶段*/
    mounted() {
      this.mountedFn()
    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
    .nextReport{
      margin: 0 auto;
      @include wh(500px,100px);
      background-color: red;
      color: white;
      @include flex(row,center,center);
    }
    .head{
      border-bottom: 20px solid $jsBgcGray;
      font-size: 36px;
      // margin: 0 auto;
      text-align: center;
      span{
        color: red;
      }
    }
    .showComment{
      .reportArticle{
        font-size: 36px;
        word-break: break-all;
        margin-left: 120px;
      }
      &:active{
        background-color: rgb(238,238,238);
      }
      border-bottom: 20px solid $jsBgcGray;
      // background-color: white;
      padding :  25px;
            // 头像//////////////////
      .headPor{
        margin-bottom: -25px;
      }
      // 被回复内容
      .replyComment{
        word-break: break-all;
        background-color: rgb(240,240,240);
        margin-left: 120px;
        padding-left: 20px;
        color: #a0a0a0;
        margin-bottom: 20px;
        font-size: 30px;
        div{
          padding:10px 0;
        }
        div:nth-child(1){
          color:rgb(190,190,190);
        }
        div:nth-child(2){
          color:rgb(104,104,104);
        }
      }
      // 评论内容
      .commentContent{
        font-size:30px;
        word-break: break-all;
        margin-left: 120px;
      }
      .commentContentForGold{
        color: $red;
        font-size:36px;
        word-break: break-all;
        margin-left: 120px;
        .gold{
          font-size: 36px;
        }
      }
      // 下部
      .bottom{
        padding-bottom: 25px;
        @include flex(row,space-between ,center)
        // margin-left: 120px;
        height: 40px;
        line-height: 40px;
        div:nth-child(1){
          color:#ccc;
          span{
            font-size: 30px;
          }
          .time{
            font-size: 30px;
            margin-left: 120px;
          }
        }
        div:nth-child(2){
          span{
            color:rgb(26,195,98);
            font-size: 30px;
          }
        }
        /* 更多*/

      }
    }

  // @import "../../assets/scss/com/List.scss";
  // 单个评论
</style>



